@use "../../variable/color" as color;
@use "../../variable/size" as size;
@use "../../variable/mixin" as mixin;
@use "../../variable/font" as font;
@use "../../variable/media" as media;

@media screen and (max-width: media.$laptop) {
    .card-container {
        padding: 30px;
    
        .border {    
            &::before {
                @include mixin.transform(translate(-50%, -50%) rotate(-35deg));                
            }
        }
    }
}
  